import React, {
  useCallback, useEffect, useState
} from 'react';
import {
  Box, Flex, List, ListItem, Radio, RadioGroup, Text, VStack,
} from '@chakra-ui/react';
import { useTranslation } from 'react-i18next';
import { useNavigate, useSearchParams } from 'react-router-dom';
import { shallowEqual } from 'react-redux';
import MiniBox from '../../../../layout/MiniBox';
import { useAppAsyncDispatch, useAppSelector } from '../../../../hooks/store';

import { listAllbusiness } from '../../../../store/middlewares/business';
import { apiErrorHandler } from '../../../../utils/general';
import Icon from '../../../../components/Icon';
import { updateSetupWalletOptions } from '../../../../store/actionCreators/account';

type AccountType = 'cash' | 'card' | 'crypto'

/**
 * Add account options screen - To get account type and business
 * @module setupWallet
 * @url /ao/account/setup-options
 * @see {@link https://www.figma.com/file/ndxr4b9PADQG9FMkIWp2ie/For-Solid-(Product)?node-id=22252%3A123290&t=p5dQisjItdBiIvfN-0}
 * @returns {JSX.Element}
 */
const SetupWalletOptions = () => {
  const { t } = useTranslation();
  const dispatch = useAppAsyncDispatch();
  const navigate = useNavigate();
  const [searchParams] = useSearchParams();

  const personDetails = useAppSelector((store) => store.user.personDetails, shallowEqual);
  const businesses = useAppSelector((store) => store.business.businesses, shallowEqual);
  const programConfig = useAppSelector((store) => store.core.programConfig, shallowEqual);

  const selectedAccountType: AccountType | null = searchParams.get('accountType') as AccountType;

  const [accountType, updateAccountType] = useState<'cash' | 'card' | 'crypto'>(selectedAccountType || 'cash');

  useEffect(() => {
    dispatch(listAllbusiness())
      .catch(apiErrorHandler);
  }, []);

  const submit = useCallback((businessId: string | null = null) => {
    dispatch(updateSetupWalletOptions({
      accountType,
      businessId
    }));
    navigate('/ao/account/setup');
  }, [accountType]);

  const addBusiness = useCallback(() => {
    navigate('/ao/kyb/businessDetails');
  }, []);

  return (
    <Box className="page">
      <form>
        <MiniBox
          varient="box"
          onBack={() => {
            navigate(-1);
          }}
          heading={t('setup_wallet_options.title')}
        >
          <Box width="100%">
            <Box width="100%">
              <Text data-testid="account-type-label" variant="large" mb="10px">
                {t('setup_wallet_options.accountType')}
              </Text>

              <RadioGroup data-testid="account-type-radiogroup" onChange={(v: 'cash' | 'card' | 'crypto') => updateAccountType(v)} value={accountType}>

                <VStack alignItems="flex-start" py="24px">
                  <Radio value="cash" variant="primary">
                    <Text variant="regular" fontSize="14px">{t('setup_wallet_options.cash')}</Text>
                  </Radio>

                  {programConfig?.bank?.accountType?.cardAccount
                    ? (
                      <Radio value="card" variant="primary">
                        <Text variant="regular" fontSize="14px">{t('setup_wallet_options.card')}</Text>
                      </Radio>
                    ) : null }

                  {/* <Radio value="crypto" variant="primary">
                    <Text variant="regular" fontSize="14px">{t('setup_wallet_options.crypto')}</Text>
                  </Radio> */}

                </VStack>
              </RadioGroup>

            </Box>

            <Box width="100%">
              <Text data-testid="add-account-title" variant="large" mb="10px">
                {t('setup_wallet_options.addAccountTo')}
              </Text>

              <List>
                {programConfig?.bank?.accountType?.personalChecking
                  ? (
                    <ListItem data-testid="personal-checking" cursor="pointer" padding="22px" mb="20px" border="1px solid #EDEFF1" onClick={() => { submit(null); }}>
                      <Flex justifyContent="space-between">
                        <Text variant="regular">
                          {`${personDetails?.firstName} ${personDetails?.lastName}`}
                        </Text>
                        <Icon icon="Arrow-small-right" size={12} className="svg-light-grey-1" />
                      </Flex>
                    </ListItem>
                  )
                  : null}

                {programConfig?.bank?.accountType?.businessChecking
                  ? (
                    <>
                      {businesses.map((b) => (
                        <ListItem
                          data-testid={`business-item-${b.id}`}
                          cursor="pointer"
                          key={`business-${b.id}`}
                          padding="22px"
                          mb="20px"
                          border="1px solid #EDEFF1"
                          onClick={() => {
                            if (b.kyb.status === 'approved') {
                              submit(b.id);
                            } else if (b.kyb.status === 'declined') {
                              navigate('/ao/kyb/declined');
                            } else {
                              navigate('/ao/kyb/declined');
                            }
                          }}
                        >
                          <Flex justifyContent="space-between">
                            <Text variant="regular" textTransform="capitalize">
                              {b.legalName}
                            </Text>
                            <Icon icon="Arrow-small-right" size={12} className="svg-light-grey-1" />
                          </Flex>
                        </ListItem>
                      ))}
                      {
                    accountType === 'cash' ? ( // card and crypto not allowed
                      <ListItem data-testid="new-business" cursor="pointer" onClick={addBusiness} padding="22px" mb="20px" border="1px solid #EDEFF1">
                        <Flex justifyContent="space-between">
                          <Text variant="regular">
                            {t('setup_wallet_options.addBusiness')}
                          </Text>
                          <Icon icon="Arrow-small-right" size={12} className="svg-light-grey-1" />
                        </Flex>
                      </ListItem>
                    ) : null
                     }
                    </>
                  ) : null }
              </List>
            </Box>
          </Box>
        </MiniBox>
      </form>
    </Box>
  );
};

export default SetupWalletOptions;
